<template>
	<nav
		class="navbar navbar-expand-lg navbar-dark bg-primary fixed-top"
		id="sideNav"
	>
		<a
			class="navbar-brand js-scroll-trigger"
			href="#page-top"
			@click="onClickCollapse()"
		>
			<span class="d-block d-lg-none">Brent Vanwildemeersch</span>
			<span class="d-none d-lg-block"
				><img
					class="img-fluid img-profile rounded-circle mx-auto mb-2"
					src="../assets/brent.png"
					alt=""
			/></span>
		</a>
		<button
			class="navbar-toggler"
			type="button"
			data-toggle="collapse"
			data-target="#navbarSupportedContent"
			aria-controls="navbarSupportedContent"
			aria-expanded="false"
			aria-label="Toggle navigation"
		>
			<span class="navbar-toggler-icon"></span>
		</button>
		<div class="collapse navbar-collapse" id="navbarSupportedContent">
			<ul class="navbar-nav">
				<li class="nav-item">
					<a
						class="nav-link js-scroll-trigger"
						:class="{ active: activeNav === 0 }"
						href="#about"
						@click="onClickNavItem(0)"
						>About</a
					>
				</li>
				<li class="nav-item">
					<a
						class="nav-link js-scroll-trigger"
						:class="{ active: activeNav === 1 }"
						href="#experience"
						@click="onClickNavItem(1)"
						>Experience</a
					>
				</li>
				<li class="nav-item">
					<a
						class="nav-link js-scroll-trigger"
						:class="{ active: activeNav === 2 }"
						href="#education"
						@click="onClickNavItem(2)"
						>Education</a
					>
				</li>
				<li class="nav-item">
					<a
						class="nav-link js-scroll-trigger"
						:class="{ active: activeNav === 3 }"
						href="#skills"
						@click="onClickNavItem(3)"
						>Skills</a
					>
				</li>
				<li class="nav-item">
					<a
						class="nav-link js-scroll-trigger"
						:class="{ active: activeNav === 4 }"
						href="#projects"
						@click="onClickNavItem(4)"
						>Projects</a
					>
				</li>
				<li class="nav-item">
					<a
						class="nav-link js-scroll-trigger"
						:class="{ active: activeNav === 5 }"
						href="#articles"
						@click="onClickNavItem(5)"
						>Tech-Blog</a
					>
				</li>
			</ul>
		</div>
	</nav>
</template>

<script>
	export default {
		name: "navigation",

		data() {
			return {
				activeNav: 0,
			};
		},

		methods: {
			onClickNavItem(navID) {
				this.activeNav = navID;
				document.querySelector(".navbar-collapse").classList.remove('show');
			},
			onClickCollapse() {
				document.querySelector(".navbar-collapse").classList.remove('show')
			},
		},
	};
</script>
