<template>
	<section class="resume-section" id="articles">
		<div class="resume-section-content">
			<h2 class="mb-5">Tech-Blog</h2>
			<div
				class="d-flex flex-column flex-md-row justify-content-between mb-5"
				v-for="article in articles"
				:key="article.id"
			>
				<div class="flex-grow-1">
					<a :href="article.canonical_url" target="_blank" rel="noreferrer"
						><h3 class="mb-0">{{ article.title }}</h3></a
					>
					<p>
						{{ article.description }}
					</p>
				</div>
				<div class="flex-shrink-0">
					<a :href="article.canonical_url" target="_blank" rel="noreferrer"
						><span class="text-primary">Read article</span></a
					>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
	export default {
		name: "Articles",

		data() {
			return {
				articles: null,
			};
		},

		created() {
			this.fetchAllArticles();
		},

		methods: {
			fetchAllArticles() {
				fetch("https://dev.to/api/articles?username=vanwildemeerschbrent")
					.then((response) => response.json())
					.then((articles) => (this.articles = articles));
			},
		},
	};
</script>
